import { Component } from "@angular/core";
import {
  NavController,
  ActionSheetController,
  LoadingController,
  AlertController
} from "ionic-angular";
import { FirebaseListObservable } from "angularfire2/database";
import { FirebaseProvider } from "./../../providers/firebase/firebase";
import { Storage } from "@ionic/storage";

@Component({
  selector: "page-home",
  templateUrl: "home.html"
})
export class HomePage {
  messages: FirebaseListObservable<any[]>;
  newMessage = {};
  message = "";
  nickname = "";

  constructor(
    public navCtrl: NavController,
    public storage: Storage,
    public firebaseProvider: FirebaseProvider,
    public actionSheetCtrl: ActionSheetController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController
  ) {}

  ionViewDidLoad() {
    this.storage.get("nickname").then(data => {
      if (data) {
        this.nickname = data;
        this.getMessages();
      } else {
        let prompt = this.alertCtrl.create({
          title: "Set Username",
          message: "Enter a name for chat",
          enableBackdropDismiss: false,
          inputs: [
            {
              name: "username",
              placeholder: "Nickname"
            }
          ],
          buttons: [
            {
              text: "Save",
              handler: data => {
                this.storage.set("nickname", data.username);
                this.nickname = data.username;
                this.getMessages();
              }
            }
          ]
        });
        prompt.present();
      }
    });
  }

  getMessages() {
    let loader = this.loadingCtrl.create({
      content: "Getting messages..."
    });
    loader.present();
    this.messages = this.firebaseProvider.getMessages();
    this.messages.subscribe(data => {
      loader.dismiss();
    });
  }

  onPressMessage(event, message) {
    let actionSheet = this.actionSheetCtrl.create({
      title: "Options",
      buttons: [
        {
          text: "Delete",
          role: "destructive",
          handler: () => {
            this.removeItem(message.$key);
          }
        },
        {
          text: "Copy",
          handler: () => {
            console.log("Copy clicked");
          }
        },
        {
          text: "Cancel",
          role: "cancel",
          handler: () => {
            console.log("Cancel clicked");
          }
        }
      ]
    });
    actionSheet.present();
  }

  sendMessage() {
    this.newMessage = {
      name: this.nickname,
      text: this.message,
      created: new Date().getTime()
    };
    this.firebaseProvider.addMessage(this.newMessage);
    this.message = "";
  }

  removeItem(id) {
    this.firebaseProvider.removeMessage(id);
  }
}



